import React, { Component } from 'react';
import './NearestMasjid.css';
import Grid from 'material-ui/Grid';
import { withStyles } from 'material-ui/styles';
import Paper from 'material-ui/Paper';
import Typography from 'material-ui/Typography';
import { LinearProgress } from 'material-ui/Progress';
import List, { ListItem, ListItemText,ListSubheader} from 'material-ui/List';
import Avatar from 'material-ui/Avatar';
import Icon from 'material-ui/Icon';
import Card, { CardHeader, CardMedia, CardContent, CardActions } from 'material-ui/Card';
import IconButton from 'material-ui/IconButton';
import MoreVertIcon from 'material-ui-icons/MoreVert';
import FavoriteIcon from 'material-ui-icons/Favorite';
import ShareIcon from 'material-ui-icons/Share';
import nophoto from '../../assets/images/nophotos.png'; 
import Modal from 'material-ui/Modal';

const styles = theme => ({
  root: theme.mixins.gutters({
    paddingTop: 16,
    paddingBottom: 16,
    marginTop: theme.spacing.unit * 1,
  }),
  marginTop:{
  	marginTop: theme.spacing.unit * 1,
  },
  avatar: {
    backgroundColor: 'red',
  },
  media: {
    height: 194,
  },
  actions: {
    display: 'flex',
  },
  modalstyle: {
    position: 'absolute',
    width: theme.spacing.unit * 80,
    backgroundColor: theme.palette.background.paper,
    boxShadow: theme.shadows[5],
    top: '50%',
    left: '50%',
    transform: 'translate(-50%, -50%)',
  },
});
class NearestMasjid extends Component{
	constructor(props){
		super(props);
		this.state = {
	      error: null,
	      isLoaded: false,
	      prayertimesdata: false,
	      prayerlocationdata: false,
	      modalopen:false,
	      selectedlocation:false,
	    };
	    this.googlemapcontainer = React.createRef();
	    console.log(this.props.geolocation)
	}
	componentDidMount() {
		const {geolocation} = this.props;
		if(geolocation){
			this.fetchPrayerTimedata(geolocation);
			this.fetchNearestPrayerLocation(geolocation);
		}
		
	}
	handleModalClose=()=>{
		this.setState({modalopen: false });
	}
	openInformationModal=(event,locationdata)=>{
		this.setState({ modalopen: true, selectedlocation:locationdata});
		
		
	}
	ModalOnrendered=()=>{
		console.log('ModalOnrendered');
		const {selectedlocation}=this.state;
		const location={
	          	lat: selectedlocation.geometry.location.lat(),
	          	lng: selectedlocation.geometry.location.lng()
	          }
		if(window.google.maps){
	        var map = new window.google.maps.Map(this.googlemapcontainer.current, {
	          zoom: 16,
	          center: location
	        });
	        var marker = new window.google.maps.Marker({
			    position: location,
			    title:selectedlocation.name
			})
			marker.setMap(map);
		}
	}
	fetchPrayerTimedata=(geolocation)=>{
		fetch(`https://api.aladhan.com/v1/timings/${Math.round((new Date()).getTime() / 1000)}?latitude=${geolocation.latitude}&longitude=${geolocation.longitude}&method=2`)
	      .then(res => res.json())
	      .then(
	        (result) => {
	        	console.log(result.data.timings);
	        	this.setState({
		            prayertimesdata:result.data.timings
		        });
	        },
	        // Note: it's important to handle errors here
	        // instead of a catch() block so that we don't swallow
	        // exceptions from actual bugs in components.
	        (error) => {
	          this.setState({
	            isLoaded: true,
	            error
	          });
	        }
	      )
	}
	fetchNearestPrayerLocation=(geolocation)=>{
		var request = {
		location: {lat: geolocation.latitude, lng: geolocation.longitude},
	    radius: '500',
	    type: ['mosque']
	  };
		let service = new window.google.maps.places.PlacesService(document.createElement('div'));
  		service.nearbySearch(request, (results, status)=>{
		  console.log(results, status);
		  	this.setState({
	            prayerlocationdata:results
	        });
		});

	}
	componentDidUpdate(prevProps, prevState, snapshot){
		const {geolocation} = this.props;
		if(geolocation.latitude !== prevProps.geolocation.latitude){
			this.fetchPrayerTimedata(geolocation);
			this.fetchNearestPrayerLocation(geolocation);
		}
	}
	ifPlaceHasPhotos=(locationdata)=>{
		const { classes} = this.props;
		if (locationdata.hasOwnProperty('photos')) {
			const photo=locationdata.photos[0];
		  return (
		  	<CardMedia
			    className={classes.media}
			    image={photo.getUrl({'maxWidth': 345, 'maxHeight': 194})}
			    title={locationdata.name}
			  />
		  )
		}
		return (
		  	<CardMedia
			    className={classes.media}
			    image={nophoto}
			    title={'default image'}
			  />
		  )
	}
	renderRatingUI=(locationdata)=>{
		let roundRating=Math.round(locationdata.rating);
		if(roundRating){
			return Array(roundRating).fill().map((empty,index)=><Icon style={{ color: '#FFBC00' }} key={index}>star_rate </Icon>);
		}return null;
		
	}
	renderPrayerLocations=()=>{
		const { classes,geolocation} = this.props;
		const {prayerlocationdata}=this.state;
		if(prayerlocationdata){
			const renderui=prayerlocationdata.map((locationdata)=>{
				return (
					<Grid key={locationdata.place_id} item xs={12} sm={4}>
					<Card>
					  <CardHeader
					    avatar={
					      <Avatar alt={locationdata.name} src={locationdata.icon} aria-label="Recipe">
					        
					      </Avatar>
					    }
					    title={locationdata.name}
					    subheader={this.renderRatingUI(locationdata)}
					  />
					  {this.ifPlaceHasPhotos(locationdata)}
					  <CardContent>
					    <Typography component="p">
					      {locationdata.vicinity}

					    </Typography>
					  </CardContent>
					  <CardActions className={classes.actions} disableActionSpacing>
					    <IconButton onClick={(event)=>this.openInformationModal(event,locationdata)}  aria-label="Add to favorites">
					    <Icon>place</Icon>
					    </IconButton>
					    <IconButton aria-label="Share">
					      <ShareIcon />
					    </IconButton>
					  </CardActions>
					</Card>
				</Grid>
				)
			})
			return (
				<div className={classes.marginTop}>
				<Grid container spacing={24}>
					{renderui}
				</Grid>
				</div>
			)
		}else{
			return(
				<Paper className={classes.root} elevation={4}>
			        <Typography variant="headline" component="h3">
			          This is a sheet of paper. {this.props.currentLocation}
			        </Typography>
			        <LinearProgress />
			      </Paper>
			)
		}
	}
	renderPrayerTimedata=()=>{
		const { classes,geolocation} = this.props;
		const {isLoaded,prayertimesdata}=this.state;
		if(prayertimesdata){
			const renderui = Object.keys(prayertimesdata).map((key)=>{
			    return (
			    	<ListItem key={key}>
			          <Avatar>
			          <Icon>
				        access_time
				      </Icon>
			          </Avatar>
			          <ListItemText primary={key} secondary={prayertimesdata[key]} />
			        </ListItem>
			    )
			});
        	return (
        		<Paper className={classes.root} elevation={4}>
			        <List
			        	subheader={<ListSubheader component="div">Nested List Items</ListSubheader>}
			        >
			        {renderui}
			      </List>
		      </Paper>
      		)
		}else{
		return (
				<Paper className={classes.root} elevation={4}>
			        <Typography variant="headline" component="h3">
			          This is a sheet of paper. {this.props.currentLocation}
			        </Typography>
			       <LinearProgress />
			     </Paper>
			)	
		}
		
	}
	renderModalContent=()=>{
		const { classes,geolocation} = this.props;
		const {selectedlocation}=this.state;
		return(
			<Card>
				<div
				ref={this.googlemapcontainer}
					style={{width:'100%',height:'250px'}}
					
				>
				</div>
		        <CardContent>
		          <Typography gutterBottom variant="headline" component="h2">
		            {selectedlocation.name}
		          </Typography>
		          <Typography component="p">
		           {selectedlocation.vicinity}
		          </Typography>
		        </CardContent>
		        <CardActions>
		          <IconButton aria-label="Share">
			      <ShareIcon />
			    </IconButton>
		        </CardActions>
		      </Card>
		)
	}
	render(){
		const { classes,geolocation} = this.props;
		const {isLoaded,prayertimesdata,modalopen,selectedlocation}=this.state;
		return(
			<div>
			<Grid container spacing={24}>
			<Grid item xs={12} sm={9}>
				{this.renderPrayerLocations()}
			</Grid>
			<Grid item xs={12} sm={3}>
				{this.renderPrayerTimedata()}
			</Grid>
			</Grid>
			<Modal
	          aria-labelledby="simple-modal-title"
	          aria-describedby="simple-modal-description"
	          open={modalopen}
	          onClose={this.handleModalClose}
	          onRendered={this.ModalOnrendered}
	        >
	          <div className={classes.modalstyle}>
	            {this.renderModalContent()}
	          </div>
	        </Modal>
			</div>
		)
	}
}
export default withStyles(styles)(NearestMasjid);



